import Axios from "axios";
import Qs from "qs";
// import XLSX from "xlsx";
import NProgress from "nprogress";
import "nprogress/nprogress.css";
NProgress.configure({
	showSpinner: false
});
const apiMethods = {
	methods: {
		apiAxiosInterceptors() {
			let _this = this;
			Axios.interceptors.request.use(
				function (config) {
					NProgress.set(0.4);
					_this.isLoading = true;
					_this.isHide = true;
					return config;
				},
				function (error) {
					return Promise.reject(error);
				}
			);
			Axios.interceptors.response.use(
				function (response) {
					NProgress.done();
					_this.isLoading = false;
					_this.isHide = false;
					return response;
				},
				function (error) {
					NProgress.done();
					_this.isLoading = false;
					_this.isHide = !_this.isHide;
					if (error && error.response) {
						switch (error.response.status) {
							case 400:
								_this.errMsg = "400,错误请求";
								break;
							case 401:
								_this.errMsg = "401,未授权，请重新登录";
								break;
							case 403:
								_this.errMsg = "403,拒绝访问";
								break;
							case 404:
								_this.errMsg = "404,请求错误,未找到该资源";
								break;
							case 405:
								_this.errMsg = "405,请求方法未允许";
								break;
							case 408:
								_this.errMsg = "408,请求超时";
								break;
							case 500:
								_this.errMsg = "500,服务器端出错";
								break;
							case 501:
								_this.errMsg = "501,网络未实现";
								break;
							case 502:
								_this.errMsg = "502,网络错误";
								break;
							case 503:
								_this.errMsg = "503,服务不可用";
								break;
							case 504:
								_this.errMsg = "504,网络超时";
								break;
							case 505:
								_this.errMsg = "505,http版本不支持该请求";
								break;
							default:
								break;
						}
					} else {
						if (error.message.includes("timeout")) {
							_this.errMsg = "请求超时";
						}
						_this.errMsg = "Oops，服务器又开小差了！";
					}
					_this.errTipsOpen();
					return false;
					//return Promise.reject(error);
				}
			);
		},
		apiGet(url) {
			return new Promise((resolve, reject) => {
				Axios.get(url).then((response) => {
					resolve(response.data)
				}, (response) => {
					reject(response)
					// _g.closeGlobalLoading()
					/*           bus.$message({
				  message: '请求超时，请检查网络',
				  type: 'warning'
				}) */
				})
			})
		},
		apiPost(url, data) {
			return new Promise((resolve, reject) => {
				Axios({
					method: "POST",
					url: url,
					data: data,
					timeout: 30000,
					transformRequest: [
						function (data) {
							let qsData = Qs.stringify(data);
							return qsData.replace(/5B[0-9]{1}/g, "5B");
						}
					]
				}).then((response) => {
					resolve(response.data)
				}).catch((response) => {
					reject(response)
					// bus.$message({
					//   message: '请求超时，请检查网络',
					//   type: 'warning'
					// })
				})
			})
		},
		// apiExcel(name) {
		// 	var elt = document.getElementById("chartTable");
		// 	var wb = XLSX.utils.table_to_book(elt, {
		// 		sheet: "Sheet JS"
		// 	});
		// 	XLSX.writeFile(wb, name + ".xlsx");
		// },
		apiTbNode(ds, col, type) {
			let _this = this;
			let tdIndex = ds.td.length - 1;

			ds.th.forEach(th => {
				let node = document.createElement("th");
				let textnode = document.createTextNode(th);
				node.appendChild(textnode);
				document.getElementById("chartsHeader").appendChild(node);
			});

			ds.td.forEach((tdi, i) => {
				let trnode = document.createElement("tr");

				tdi.forEach((tdj, j) => {
					let tbd = document.createElement("td");

					if (i == tdIndex) {
						if (j == 0) {
							tbd.setAttribute("colspan", col);
						}
						if (j == 3) {
							_this.arrowType = tdj;
							_this.arrowFn();
							tdj = _this.arrowType;
						}
					}
					if (type == 5) {
						if (j == 5) {
							_this.arrowType = tdj;
							_this.arrowFn();
							tdj = _this.arrowType;
						}
					}
					tbd.innerHTML = tdj;
					trnode.append(tbd);
				});

				document.getElementById("chartsBody").appendChild(trnode);
			});
		},
		apiColor() {
			let colorArr = [
				"#60acfc",
				"#feb64d",
				"#5bc49f",
				"#32d3eb",
				"#ff7c7c",
				"#9287e7",
				"#39B3EA",
				"#40CEC7",
				"#D4EC59",
				"#FA816D",
				"#D660A8",
				"6370DE",
				"#5B8FF9",
				"#5AD8A6",
				"#5D7092",
				"#F6BD16",
				"#E86452",
				"#6DC8EC",
				"#945FB9",
				"#FF9845",
				"#1E9493",
				"#FF99C3"
			]
			return colorArr
		},
		arrowFn() {
			let _this = this;
			switch (_this.arrowType) {
				case "0":
					_this.arrowType = "<span >-</span>";
					break;
				case "1":
					_this.arrowType = "<span style='color:red'>&uarr;</span>";
					break;
				case "-1":
					_this.arrowType = "<span style='color:green'>&darr;</span>";
					break;
				default:
					break;
			}
		},
		errTipsOpen() {
			let _this = this;
			_this.$notify.error({
				title: _this.errMsg,
				position: "bottom-right"
			});
		}
	},
	computed: {

	}
}

export default apiMethods