import G2 from "@antv/g2";
import Ds from "@antv/data-set";
import Api from "@/components/utils/api";
import moment from "moment";
const uuidv4 = require('uuid/v4')

export default {
  computed: {
    G2: function () {
      if (typeof window !== 'undefined' && window.G2) {
        return window.G2
      } else {
        return G2
      }
    },
    Ds: function () {
      if (typeof window !== 'undefined' && window.Ds) {
        return window.Ds
      } else {
        return Ds
      }
    },
    id: function () {
      return uuidv4()
    }
  },
  data() {
    return {
      chart: null,
      postUrl: "http://172.28.30.13:8082/selectController/select",
      ds: {}
    }
  },
  watch: {
    // 监控data，当发生变化时，重新绘制图表
    // data: function (val, oldVal) {
    //   console.table(val)
    //   // this.chart.changeData(val)
    // }
  },
  methods: {
    // 构造图表实例
    constructChart() {
      // 销毁实例
      // if (this.chart) {
      //   this.chart.destroy()
      // }
      // const dom = document.getElementById(this.id)
      // if (dom && dom.innerHTML) {
      //   dom.innerHTML = ''
      // }

      // let _this = this;
      // _this.apiPost(_this.postUrl, _this.postDs).then(res => {
      //   _this.ds = res;
      //   // _this.columnStack();
      // });
      let _this = this;
      return new this.G2.Chart({
        container: _this.id,
        forceFit: true,
        padding: _this.cPadding,
        height: _this.cHeight
      })
    },
    initFn() {
      let _this = this;
      // if (_this.dateChk.length == 0) {
      //   return false;
      // } else {



        if (_this.rel == 5) {
          let lastMonData = [
            moment()
            .month(moment(_this.postDs.time[0]).month() - 1)
            .startOf("month")
            .valueOf(),
            moment()
            .month(moment(_this.postDs.time[0]).month() - 1)
            .endOf("month")
            .valueOf()
          ];
          _this.postDs.time = _this.postDs.time.concat(lastMonData);
        }



        _this.apiAxiosInterceptors();

        _this.apiPost(_this.postUrl, _this.postDs).then(res => {
          if (_this.rel == 5){
            _this.postDs.time = _this.postDs.time.splice(0, 2);
          }
          _this.ds = res;
          _this.drawChart(_this.ds.chartData);
          _this.apiTbNode(_this.ds.tableData, _this.colspan,_this.rel);
        });


      // }
    },
    drawChart(data) {
      let _this = this
      // 新建实例
      _this.chart = _this.constructChart()

      // 配置图表
      _this.setChartConfig(data)

      // 绘制
      _this.chart.render()

      // if(_this.rel==5){
      //   _this.dateChk = _this.dateChk.splice(0, 2);
      // }

      // let _this = this
      // // 销毁实例
      // this.$once('hook:beforeDestroy', function () {
      //   _this.chart.destroy()
      // })


    },
    // getExcel() {
    //   let _this = this;
    //   _this.apiExcel(_this.mTitle);
    // }
  },
  mounted() {
    this.initFn();
    // this.drawChart(this.ds)
  },
  mixins: [Api]
}