<template>
  <div>
    <div :id="id" :rel="rel"></div>
    <mz-table :name="mTitle" :tbShow="tbShow" />
  </div>
</template> 
<script>
import chartMix from "@/components/utils/chart";
import mzTable from "@/components/plugins/table";
export default {
  name: "mz-stack",
  data() {
    return {
      colspan: 3,
      cPadding: [50, 0, 100, 30],
      cHeight: 500
    };
  },
  props: {
    rel: Number,
    mTitle: String,
    postDs: Object,
    tbShow: Boolean
  },
  created() {},
  mounted() {},
  methods: {
    setChartConfig(data) {
      let _this = this;
      let ds = new _this.Ds();
      let dv = ds.createView().source(data);
      _this.chart.source(dv);
      _this.chart.scale({
        counts: {
          tickCount: 10
        }
      });
      _this.chart.guide().text({
        content: _this.mTitle,
        position: ["50%", "5%"],
        offsetY: -50,
        style: {
          fontSize: 24,
          fill: "gray",
          textAlign: "center"
        }
      });
      _this.chart
        .intervalStack()
        .position("employee*counts")
        .color("device", _this.apiColor());
    }
  },
  components: {
    mzTable
  },
  mixins: [chartMix]
};
</script>
 
