    v-if="isLogin==true"
  >
    <el-main style="padding:50px">
      <el-row id="selectOpt" class="row-padding">
        <el-col :span="2" style="text-align:right;white-space:nowrap;line-height:42px;">日期选择：</el-col>
        <el-col :span="6">
          <el-date-picker
            v-model="postDs.time"
            type="daterange"
            align="right"
            size="large"
            unlink-panels
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="timestamp"
            @change="cleanSelect"
          ></el-date-picker>
        </el-col>
        <el-col :span="2" style="text-align:right;white-space:nowrap;line-height:42px;">实际工作日：</el-col>
        <el-col :span="4">
          <el-input-number
            id="workingDay"
            size="large"
            style="width:100%"
            v-model="postDs.workingDay"
            controls-position="right"
            :min="1"
            :max="360"
          ></el-input-number>
        </el-col>
        <el-col :span="2" style="text-align:right;white-space:nowrap;line-height:42px;">生成图表：</el-col>
