              v-for="item in selectOptions"
              :key="item.id"
              :label="item.label"
              :value="[item.chartType,item.label]"
            ></el-option>
          </el-select>
        </el-col>
        <el-col :span="2">
          <el-button type="primary" size="large" @click="linkFn" icon="el-icon-edit-outline
">修改信息</el-button>
        </el-col>
      </el-row>

      <el-row id="chartRow">
        <el-col :span="20" :offset="2">
          <mz-stack
            v-if="postDs.time.length>0 && chartOpt[0]==1"
            :mTitle="chartOpt[1]"
            :postDs="postDs"
            :rel="chartOpt[0]"
            :tbShow="isHide"
          />
          <mz-dodge
            v-else-if="postDs.time.length>0 && chartOpt[0]==2"
            :mTitle="chartOpt[1]"
            :postDs="postDs"
            :rel="chartOpt[0]"
            :tbShow="isHide"
          />
          <mz-basic
            v-else-if="postDs.time.length>0 && chartOpt[0]==3"
            :mTitle="chartOpt[1]"
            :postDs="postDs"
            :rel="chartOpt[0]"
            :tbShow="isHide"
          />
          <mz-dodge2
            v-else-if="postDs.time.length>0 && chartOpt[0]==4"
            :mTitle="chartOpt[1]"
            :postDs="postDs"
            :rel="chartOpt[0]"
            :tbShow="isHide"
          />
          <mz-doubleAxes
            v-else-if="postDs.time.length>0 && chartOpt[0]==5"
            :mTitle="chartOpt[1]"
            :postDs="postDs"
            :rel="chartOpt[0]"
            :tbShow="isHide"
          />
        </el-col>
      </el-row>
    </el-main>
  </el-container>
</template>

<script>
import Api from "@/components/utils/api";
// import mzStack from "@/components/plugins/stack";
// import mzDodge from "@/components/plugins/dodge";
// import mzBasic from "@/components/plugins/basic";
// import mzDodge2 from "@/components/plugins/dodge2";
// import mzDoubleAxes from "@/components/plugins/doubleAxes";
export default {
  data() {
    return {
      selectOptions: [
        {
          chartType: 1,
          label: "个人设备类型单数统计图"
        },
        {
          chartType: 2,
          label: "驻点设备类型单数统计图"
        },
        {
          chartType: 3,
          label: "个人工单总数统计图"
        },
        {
          chartType: 4,
          label: "驻点人均单数统计图"
        },
        {
          chartType: 5,
          label: "个人工单总数对比情况图"
        }
      ],
      bodyHeight: window.innerHeight,
      errMsg: "",
      // errMsgArr: [],
      chartOpt: [],
      // dateChk: "",
      // workingDay: "",
      errTemp: "",
      isLoading: false,
      isHide: true,
      isLogin: false,
      // isRender: false,
      // isClear: false,
      colspan: 0,
      arrowType: "",
      usrName: "",
      // cids: ["", "stack", "dodge", "basic", "dodge2", "doubleAxes"],
      // colorArr: [
      //   "#60acfc",
      //   "#feb64d",
      //   "#5bc49f",
      //   "#32d3eb",
      //   "#ff7c7c",
      //   "#9287e7",
      //   "#39B3EA",
      //   "#40CEC7",
      //   "#D4EC59",
      //   "#FA816D",
      //   "#D660A8",
      //   "6370DE",
      //   "#5B8FF9",
      //   "#5AD8A6",
      //   "#5D7092",
      //   "#F6BD16",
      //   "#E86452",
      //   "#6DC8EC",
      //   "#945FB9",
      //   "#FF9845",
      //   "#1E9493",
      //   "#FF99C3"
      // ],
      // chartData: {},

      // postUrl: "http://172.28.11.5:8080/selectController/select",
      // testUrl: "http://chart.tt/api/api.json",
      postDs: {
        chartType: "",
        time: [],
        workingDay: ""
      }
    };
  },
  created() {
    this.lsLoginFn();
  },
  mounted() {},
  methods: {
    buildChart() {
      let _this = this;
      if (_this.postDs.time.length == 0) {
        _this.errMsg = "请选择起止日期";
        _this.errTipsOpen();
      } else {
        _this.postDs.chartType = _this.chartOpt[0];
        _this.apiAxiosInterceptors();
      }
    },
    linkFn() {
      window.location.href = "http://172.28.30.13:8082/update.html";
    },
    getUrlParam(name) {
      let reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
      let r = window.location.search.substr(1).match(reg);
      if (r != null) return unescape(r[2]);
      return null;
    },
    lsLoginFn() {
      let _this = this;
      _this.usrName = _this.getUrlParam("username");
      if (_this.usrName == null || _this.usrName.length == 0 || _this.usrName!= "admin") {
        window.location.href = "http://172.28.30.13:8082/login.html";
      } else {
        _this.isLogin = true;
      }
    },
    cleanSelect() {
      let _this = this;
      _this.chartOpt = "";
      _this.arrowType = "";
      _this.colspan = 0;
      _this.isHide = true;
    }
    // errTipsOpen() {
    //   let _this = this;

    //   _this.$notify.error({
    //     title: _this.errMsg,
    //     position: "bottom-right"
    //   });
    // }
  },
  components: {
    // mzStack,
    // mzDodge,
