<template>
  <div>
    <div :id="id" :rel="rel"></div>
    <mz-table :name="mTitle" :tbShow="tbShow" />
  </div>
</template> 
<script>
import chartMix from "@/components/utils/chart";
import mzTable from "@/components/plugins/table";
export default {
  name: "mz-dodge2",
  data() {
    return {
      colspan: 0,
      cPadding:[50, 0, 100, 30],
      cHeight:500
    };
  },
  props: {    
    rel: Number,
    mTitle: String,
    postDs: Object,
    tbShow: Boolean
  },
  created() {},
  mounted() {},
  methods: {
    setChartConfig(data) {
      let _this = this;
      let ds = new _this.Ds();
      let dv = ds.createView().source(data);
      _this.chart.source(dv);
      _this.chart.scale({
        counts: {
          tickCount: 10
        }
      });
      _this.chart.guide().text({
        content:  _this.mTitle,
        position: ["50%", "5%"],
        offsetY: -50,
        style: {
          fontSize: 24,
          fill: "gray",
          textAlign: "center"
        }
      });
      _this.chart
        .interval()
        .position("group*counts")
        .color("type", _this.apiColor())
        .adjust([
          {
            type: "dodge",
            marginRatio: 1 / 32
          }
        ]);
    }
  },
  components: {
    mzTable
  },
  mixins: [chartMix]
};
</script>
 
