<template>
  <div :class="{hideme:tbShow}">
    <el-button
      id="exportBtn"
      type="primary"
      @click="getExcel"
      icon="el-icon-download"
      size="medium"
    >导出表格</el-button>
    <table id="chartTable" cellpadding="0" cellspacing="0">
      <thead>
        <tr id="chartsHeader"></tr>
      </thead>
      <tbody id="chartsBody"></tbody>
      <tfoot id="chartsFoot"></tfoot>
    </table>
  </div>
</template> 
<script>
import XLSX from "xlsx";
export default {
  name: "mz-table",
  props: {
    name: String,
    tbShow: Boolean
  },
  methods: {
    getExcel() {
      var elt = document.getElementById("chartTable");
      var wb = XLSX.utils.table_to_book(elt, {
        sheet: "Sheet JS"
      });
      XLSX.writeFile(wb, this.name + ".xlsx");
    }
  }
};
</script>
 
