<template>
  <div>
    <div :id="id" :rel="rel"></div>
    <mz-table :name="mTitle" :tbShow="tbShow" />
  </div>
</template> 
<script>
import chartMix from "@/components/utils/chart";
import mzTable from "@/components/plugins/table";
export default {
  name: "mz-doubleAxes",
  data() {
    return {
      colspan: 3,
      cPadding: [50, 30, 100, 30],
      cHeight: 500
    };
  },
  props: {
    rel: Number,
    mTitle: String,
    postDs: Object,
    tbShow: Boolean
  },
  created() {},
  mounted() {},
  methods: {
    setChartConfig(data) {
      let _this = this;
      let ds = new _this.Ds();
      let dv = ds.createView().source(data);
      _this.chart.source(dv, {
        lMonth: {
          min: 0
        },
        tMonth: {
          min: 0
        }
      });
      _this.chart.legend({
        custom: true,
        allowAllCanceled: true,
        items: [
          {
            value: "上月合计（单）",
            marker: { symbol: "square", fill: "#3182bd", radius: 5 }
          },
          {
            value: "本月合计（单）",
            marker: {
              symbol: "hyphen",
              stroke: "#ED7D31",
              radius: 5,
              lineWidth: 3
            }
          }
        ]
      });
      _this.chart.axis("tMonth", {
        grid: null,
        label: {
          textStyle: {
            fill: "#ED7D31"
          }
        }
      });
      _this.chart.scale({
        tMonth: {
          alias: "本月合计（单）",
          min: 0,
          tickCount: 10
        },
        lMonth: {
          alias: "上月合计（单）",
          min: 0,
          tickCount: 10
        }
      });
      _this.chart.guide().text({
        content: _this.mTitle,
        position: ["50%", "5%"],
        offsetY: -50,
        style: {
          fontSize: 24,
          fill: "gray",
          textAlign: "center"
        }
      });
      _this.chart
        .interval()
        .position("employee*lMonth")
        .color("#3182bd");
      _this.chart
        .line()
        .position("employee*tMonth")
        .color("#fdae6b")
        .size(3)
        .shape("smooth");
      _this.chart
        .point()
        .position("employee*tMonth")
        .color("#fdae6b")
        .size(3)
        .shape("circle");
    }
  },
  components: {
    mzTable
  },
  mixins: [chartMix]
};
</script>
 
